
import react, { Component,useState,useEffect,lazy,Suspense } from "react";
import { useInView } from "react-intersection-observer";

import "./App.css";


import Skills from "./Componentes/skills";
import Services from "./Componentes/services";
import Proyect from "./Componentes/proyect";
import Form from "./Componentes/form";
import Loader from "./Componentes/loader";
const Nav =lazy(()=> import("./Componentes/Nav"))

const Me = lazy(()=> import("./Componentes/me")) 

const  Cuerpo = ()=> {
 


  const [boleanwidth, setBolean]= useState(false)
const Ancho = window.screen.width
  



  useEffect(()=>{
window.onresize= ()=>{
  if(Ancho > 800){
    setBolean({boleanwidth: true})
   }else{
     setBolean({boleanwidth: false})
   }
}
  },[Ancho])
  return (
    <div>
      <Suspense fallback={ <Loader></Loader>}><Nav></Nav></Suspense>
      
    
      <Suspense fallback={<h1>Cargando</h1>} ><Me ></Me></Suspense>
      
      
      <Skills></Skills>
      <Services></Services>
      <Proyect></Proyect>
      <Form></Form>
    
    </div>)
}

class App extends Component {
 state = { isLoading: true }

componentDidMount() {
    setTimeout(()=>{
      this.setState({isLoading: false})
    },1000)
}


  render() {
 
    return (this.state.isLoading ? <Loader></Loader> : <Cuerpo></Cuerpo>)
  }
}

export default react.memo(App);
